section#admin-notice {
    &.revise {
        label {
            display: flex;
            align-items: center;
            height: 30px;
            font-size: 14px;
            margin-bottom: 0.7rem;

            @media (min-width: 768px) {
                height: 35px;
                font-size: 16px;
            }

            span {
                min-width: 26px;

                @media (min-width: 768px) {
                    min-width: 30px;
                }
            }

            input[type='text'] {
                width: 100%;
                height: 100%;
                border: 0;
                border-left: 1px solid rgba(0, 0, 0, 0.5);
                margin-left: 1rem;
                padding-left: 1rem;

                @media (min-width: 768px) {
                    height: 35px;
                    font-size: 16px;
                }
            }
        }

        .edit-area {
            width: 100%;
            min-height: 500px;
        }
    }
}
