.order--bundle-component {
    font-size: 12px;
    margin-top: 3rem;
    margin-bottom: 6rem;
    border-bottom: 1px solid rgba(112, 112, 112, 1);

    @media (min-width: 768px) {
        font-size: 14px;
        margin-bottom: 5rem;
    }

    .confirm--button--component {
        min-width: 65px;
        max-width: 65px;
        max-height: 26px;
        margin-left: 0;
        font-size: 11px;

        @media (min-width: 768px) {
            min-width: 90px;
            max-width: 90px;
            max-height: 30px;
            font-size: 13px;
        }
    }

    .head {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 40px;
        padding: 0.5rem;
        border-top: 5px solid rgba(62, 62, 62, 1);
        border-bottom: 1px solid rgba(112, 112, 112, 1);
        background-color: rgb(244, 244, 244);

        @media (min-width: 768px) {
            min-height: 50px;
        }

        .left {
            flex: 1;

            p.order--number {
                .date {
                    font-size: 0.75em;
                    margin-top: 0.3rem;
                    opacity: 0.8;
                    display: block;

                    @media (min-width: 768px) {
                        display: inline;
                        margin-left: 0.5rem;
                    }

                    &::before {
                        content: '[ ';
                    }

                    &::after {
                        content: ' ]';
                    }
                }
            }
        }

        .right {
            min-width: 120px;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            @media (min-width: 768px) {
                min-width: 250px;
            }

            .total--amount {
                &::before {
                    content: '총 ';
                }

                span {
                    font-size: 0.75em;
                    color: rgba(62, 62, 62, 0.78);

                    &::before {
                        content: '원';
                        margin-right: 0.5rem;
                        font-size: 1.35em;
                        color: rgba(62, 62, 62, 1);

                        @media (min-width: 768px) {
                        }
                    }
                }
            }

            .detail-btn {
                position: absolute;
                top: -36px;
                right: 0;

                @media (min-width: 768px) {
                    position: relative;
                    top: auto;
                    margin-left: 1rem;
                }
            }
        }
    }

    ul.list {
        position: relative;

        li {
            display: flex;
            padding: 0.8rem 0.4rem;

            &.state--wrapper {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                min-width: 70px;
                font-size: 11px;
                padding: 0;
                border: 0;
                position: absolute;
                top: 0;
                right: 0;
                width: 100px;
                height: 100%;
                background-color: white;

                @media (min-width: 768px) {
                    min-width: 150px;
                    font-size: 13px;
                }
                @media (min-width: 1366px) {
                    min-width: 180px;
                }

                .state {
                    margin-bottom: 0.5rem;
                }

                .cancle {
                    color: rgb(190, 23, 23);
                }
            }

            // .first {
            //     min-width: 28px;

            //     @media (min-width: 768px) {
            //         min-width: 40px;
            //     }
            // }

            .second {
                min-width: 33px;
                font-size: 11px;

                @media (min-width: 768px) {
                    font-size: 13px;
                    min-width: 45px;
                }
            }

            .third {
                flex: 1;

                figure {
                    img {
                        float: left;
                        width: 80px;
                        height: 80px;
                        object-fit: cover;

                        @media (min-width: 768px) {
                            width: 120px;
                            height: 120px;
                        }
                    }

                    figcaption {
                        margin-left: 90px;
                        white-space: nowrap;

                        @media (min-width: 768px) {
                            margin-left: 130px;
                        }

                        p.name {
                            margin-bottom: 0.5rem;
                        }

                        span.price {
                            &::after {
                                content: '원';
                            }
                        }

                        small {
                            font-size: 11px;
                            margin-left: 0.3rem;
                            opacity: 0.8;

                            &::before {
                                content: ' [';
                            }
                            &::after {
                                content: '개]';
                            }
                        }
                    }
                }
            }

            // .fourth {
            // }
        }
    }

    .under {
        margin-top: 0.4rem;

        @media (min-width: 768px) {
            margin-top: 1rem;
        }
    }
}
