section#admin-product {
    position: relative;

    &.revise {
        .container-500 {
            @media (min-width: 1366px) {
                margin: 0;
            }

            &.center {
                margin: 0 auto;
            }
        }
        .preview--wrap {
            position: relative;
            width: 100%;
            min-width: 300px;
            max-width: 300px;
            min-height: 300px;
            max-height: 300px;
            margin: 0 auto;
            margin-bottom: 3rem;
            overflow: hidden;
            // border: 1px solid rgba(112, 112, 112, 0.5);

            &:after {
                content: '상품 대표이미지 파일을 선택하세요.';
                font-size: 12px;
                color: rgba(62, 62, 62, 0.5);
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                top: 50%;
                left: 50%;
                width: 99%;
                height: 99%;
                transform: translate(-50%, -50%);
                border: 1px solid rgba(112, 112, 112, 0.5);
                white-space: nowrap;
                z-index: -1;
            }

            @media (min-width: 768px) {
                min-width: 330px;
                max-width: 330px;
                min-height: 330px;
                max-height: 330px;
            }
            @media (min-width: 1024px) {
                min-width: 350px;
                max-width: 350px;
                min-height: 350px;
                max-height: 350px;
            }
            @media (min-width: 1366px) {
                position: absolute;
                top: 88px;
                right: 0;
                min-width: 370px;
                max-width: 370px;
                min-height: 370px;
                max-height: 370px;
                margin: 0;
            }

            img {
                min-width: 300px;
                max-width: 300px;
                min-height: 300px;
                max-height: 300px;
                object-fit: cover;

                @media (min-width: 768px) {
                    min-width: 330px;
                    max-width: 330px;
                    min-height: 330px;
                    max-height: 330px;
                }
                @media (min-width: 1024px) {
                    min-width: 350px;
                    max-width: 350px;
                    min-height: 350px;
                    max-height: 350px;
                }
                @media (min-width: 1366px) {
                    min-width: 370px;
                    max-width: 370px;
                    min-height: 370px;
                    max-height: 370px;
                }
            }
        }
    }
}
