section#payment {
    padding: 0 1rem;

    @media (min-width: 768px) {
        padding: 0 2rem;
    }

    article.container {
        position: relative;
        max-width: 1000px;
        border-top: 5px solid rgb(62, 62, 62);
        border-bottom: 3px solid rgb(62, 62, 62);
        margin: 0 auto;
        margin-top: 10rem;
        padding: 6rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;

        &.ing {
            max-width: 600px;
            border: 0;
            border: 1px solid rgb(112, 112, 112);
            padding: 8rem 0;

            @media (min-width: 768px) {
                padding: 10rem 0;
            }
            @media (min-width: 1440px) {
                padding: 12rem 0;
            }
        }

        @media (min-width: 768px) {
            padding: 8rem 0;
        }
        @media (min-width: 1024px) {
            margin-top: 6rem;
        }
        @media (min-width: 1366px) {
            margin-top: 12rem;
        }
        @media (min-width: 1440px) {
            margin-top: 8rem;
        }
        @media (min-width: 1680px) {
            margin-top: 12rem;
        }

        h1 {
            font-weight: bold;
            font-size: 16px;

            @media (min-width: 768px) {
                font-size: 20px;
            }
        }

        p {
            font-size: 13px;
            margin-top: 1.5rem;

            @media (min-width: 768px) {
                font-size: 14px;
            }
        }

        .confirm--button--component {
            max-width: 300px;
            margin: 0;
            margin-top: 3rem;
            background-color: white;
            color: rgb(128, 211, 142);
            border: 1px solid rgb(112, 112, 112);

            @media (min-width: 768px) {
                height: 38px !important;
            }
        }

        .loading-wrap {
            width: 150px;
            display: flex;
            justify-content: space-around;
            margin-top: 2.5rem;

            div {
                background-color: rgba(112, 112, 112, 1);
                width: 10px;
                height: 10px;
                border-radius: 50%;
                animation: twinkle 1s alternate infinite;

                @keyframes twinkle {
                    from {
                        opacity: 1;
                    }
                    to {
                        opacity: 0.1;
                    }
                }

                &:nth-child(2) {
                    animation-delay: 0.25s;
                }
                &:nth-child(3) {
                    animation-delay: 0.5s;
                }
                &:nth-child(4) {
                    animation-delay: 0.75s;
                }
                &:nth-child(5) {
                    animation-delay: 1s;
                }
            }
        }
    }
}
