section#refering-nomember-detail,
section#refering-detail {
    ul.step-state--component {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        padding: 1rem 0;

        @media (min-width: 768px) {
            padding: 2rem 0;
            justify-content: space-between;
            flex-wrap: nowrap;
        }

        &.canceled {
            padding-top: 2rem;
            justify-content: center;

            &::before {
                content: '주문이 취소되었습니다.';
                color: #fc5c65;
                font-size: 16px;

                @media (min-width: 768px) {
                    font-size: 18px;
                }
            }

            & > li {
                display: none;
            }
        }

        li {
            position: relative;
            width: 33%;
            display: flex;
            justify-content: center;

            @media (min-width: 768px) {
                width: 20%;
            }

            &.active {
                figure {
                    background-color: rgb(128, 211, 142);
                    img {
                        &.disable {
                            display: none;
                        }

                        &.able {
                            display: block;
                        }
                    }
                }
            }

            &::after {
                content: '';
                display: inline-block;
                position: absolute;
                top: 50%;
                right: 0%;
                transform: translate(50%, -50%);
                width: 40px;
                height: 40px;
                background-image: url('/assets/images/icons/chevron-right.svg');
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                opacity: 0.3;

                @media (min-width: 1024px) {
                    width: 60px;
                    height: 60px;
                }

                @media (min-width: 1440px) {
                    width: 70px;
                    height: 70px;
                }
            }

            &:nth-child(3) {
                &::after {
                    display: none;
                }

                @media (min-width: 768px) {
                    &::after {
                        display: block;
                    }
                }
            }

            &:nth-child(4) {
                margin-top: 3rem;

                &::before {
                    content: '';
                    display: inline-block;
                    position: absolute;
                    top: 50%;
                    left: 0%;
                    transform: translate(-50%, -50%);
                    width: 40px;
                    height: 40px;
                    background-image: url('/assets/images/icons/chevron-right.svg');
                    background-size: contain;
                    background-position: center;
                    background-repeat: no-repeat;
                    opacity: 0.3;
                }
                @media (min-width: 768px) {
                    margin-top: 0;

                    &::before {
                        display: none;
                    }
                }
            }

            &:last-child {
                margin-top: 3rem;

                &::after {
                    display: none;
                }

                @media (min-width: 768px) {
                    margin-top: 0;
                }
            }

            figure {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 70px;
                height: 70px;
                border: 1px solid rgba(112, 112, 112, 0.2);
                border-radius: 50%;
                overflow: hidden;

                @media (min-width: 768px) {
                    width: 90px;
                    height: 90px;
                }
                @media (min-width: 1024px) {
                    width: 100px;
                    height: 100px;
                }
                @media (min-width: 1366px) {
                    width: 140px;
                    height: 140px;
                }

                img {
                    width: 65%;
                    height: 65%;
                    object-fit: contain;

                    &.disable {
                        display: block;
                    }

                    &.able {
                        display: none;
                    }
                }

                figcaption {
                    position: absolute;
                    left: 50%;
                    bottom: -1.7rem;
                    transform: translateX(-50%);
                    font-size: 12px;

                    @media (min-width: 1024px) {
                        font-size: 14px;
                    }
                }
            }
        }
    }

    .head {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 40px;
        padding: 0.5rem;
        border-top: 5px solid rgba(62, 62, 62, 1);
        border-bottom: 1px solid rgba(112, 112, 112, 1);
        background-color: rgb(244, 244, 244);

        @media (min-width: 768px) {
            min-height: 50px;
        }

        .left {
            flex: 1;

            p.order--number {
                .date {
                    font-size: 0.75em;
                    margin-top: 0.3rem;
                    opacity: 0.8;
                    display: block;

                    @media (min-width: 768px) {
                        display: inline;
                        margin-left: 0.5rem;
                    }

                    &::before {
                        content: '[ ';
                    }

                    &::after {
                        content: ' ]';
                    }
                }
            }
        }

        .right {
            min-width: 120px;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            @media (min-width: 768px) {
                min-width: 250px;
            }

            .total--amount {
                &::before {
                    content: '총 ';
                }

                span {
                    font-size: 0.75em;
                    color: rgba(62, 62, 62, 0.78);

                    &::before {
                        content: '원';
                        margin-right: 0.5rem;
                        font-size: 1.35em;
                        color: rgba(62, 62, 62, 1);

                        @media (min-width: 768px) {
                        }
                    }
                }
            }
        }
    }

    table {
        width: 100%;

        thead,
        tbody {
            tr {
                height: 50px;
                th,
                td {
                    vertical-align: middle;

                    &:first-child,
                    &:nth-child(3) {
                        width: 10%;
                    }

                    &:nth-child(2) {
                        width: 60%;
                    }

                    &:last-child {
                        width: 20%;
                    }
                }
            }
        }

        thead {
            tr {
                border-bottom: 1px solid rgb(112, 112, 112);

                th {
                    font-weight: bold;
                }
            }
        }

        tbody {
            tr {
                border-bottom: 1px solid rgba(112, 112, 112, 0.2);

                &:last-child {
                    border-bottom: 1px solid rgb(112, 112, 112);
                }

                td {
                    padding: 0.5rem 0;
                    text-align: center;

                    &:nth-child(2) {
                        text-align: left;

                        figure {
                            // min-width: 80px;
                            // min-height: 80px;
                            // max-width: 80px;
                            // max-height: 80px;
                            display: flex;
                            align-items: center;

                            @media (min-width: 768px) {
                                // min-width: 120px;
                                // min-height: 120px;
                                // max-width: 120px;
                                // max-height: 120px;
                            }

                            img {
                                width: 80px;
                                height: 80px;
                                object-fit: cover;

                                @media (min-width: 768px) {
                                    width: 120px;
                                    height: 120px;
                                }
                            }

                            figcaption {
                                margin-left: 0.7rem;
                                white-space: nowrap;
                            }
                        }
                    }

                    &:last-child {
                        .price {
                            &::after {
                                content: '원';
                            }
                        }
                    }
                }
            }
        }
    }

    ul.order--board {
        position: relative;
        margin: 4rem 0 1rem;

        @media (min-width: 768px) {
            margin: 5rem 0 1rem;
        }

        li {
            min-height: 60px;
            display: flex;
            border-bottom: 1px solid rgb(112, 112, 112);
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 14px;
            }

            &:first-child {
                min-height: 40px;
                border-bottom: 5px solid rgb(62, 62, 62);
                font-weight: bolder;
                font-size: 14px;

                @media (min-width: 768px) {
                    font-size: 15px;
                }
            }

            .title {
                width: 100%;
                max-width: 90px;
                padding: 23px 0;
                display: flex;
                // align-items: center;
                background-color: rgb(244, 244, 244);
                justify-content: center;
                font-weight: bolder;

                @media (min-width: 768px) {
                    max-width: 277px;
                    justify-content: flex-start;
                    padding-left: 2rem;
                }
            }

            .description {
                position: relative;
                flex: 1;
                display: flex;
                align-items: center;
                padding-left: 1rem;

                @media (min-width: 768px) {
                    padding-left: 2rem;
                }
            }
        }
    }
}
