nav#admin-navigation {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 99;
    width: 100%;
    height: 50px;
    background-color: white;

    @media (min-width: 768px) {
        height: 60px;
    }

    @media (min-width: 1024px) {
        top: 0;
        left: 0;
        height: 100vh;
        max-width: 250px;
        overflow: hidden;
        border-right: 1px solid rgb(112, 112, 112);
    }
    @media (min-width: 1366px) {
        max-width: 300px;
    }

    ul.menu-wrap {
        font-size: 14px;

        @media (min-width: 768px) {
            font-size: 15px;
        }
        @media (min-width: 1024px) {
            text-align: center;
        }
        @media (min-width: 1366px) {
            font-size: 16px;
        }

        li {
            font-weight: lighter;
            color: rgba(62, 62, 62, 0.5);
            margin-bottom: 2rem;

            &.active {
                font-weight: bold;
                color: rgba(62, 62, 62, 1);
            }
        }
    }

    .logout {
        position: absolute;
        bottom: 5%;
        display: flex;
        width: 100%;
        font-size: 11px;

        @media (min-width: 1024px) {
            left: 50%;
            transform: translate(-50%, 0);
            justify-content: center;
            font-size: 12px;
        }

        p {
            margin-right: 1.5rem;
            cursor: pointer;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    .mobile {
        background-color: white;

        @media (min-width: 1024px) {
            display: none;
        }

        figure.logo {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 90px;
            cursor: pointer;
        }

        .admin-menu--icon {
            position: absolute;
            top: 0.8rem;
            right: 1rem;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            width: 22px;
            height: 24px;
            cursor: pointer;

            @media (min-width: 768px) {
                top: 1.2rem;
                right: 2rem;
                transform: scale(1.2);
            }

            div {
                width: 6px;
                height: 6px;
                border: 1px solid rgb(62, 62, 62);
            }
        }

        .cover {
            position: absolute;
            top: 0;
            right: -100%;
            z-index: 1;
            width: 100%;
            height: 100vh;
            background-color: rgba(0, 0, 0, 0.5);
            transition: 0.4s;

            &.active {
                right: 0;
            }
        }

        .container {
            position: absolute;
            top: 0;
            right: -100%;
            z-index: 2;
            width: 100%;
            max-width: 300px;
            height: 100vh;
            background-color: white;
            box-shadow: 1px 0 8px 5px rgba(0, 0, 0, 0.1);
            transition-duration: 0.5s;
            transition-delay: 0.1s;
            padding-left: 3rem;
            padding-top: 13rem;
            overflow: hidden;

            &.active {
                right: 0;
            }

            figure.close {
                position: absolute;
                top: 1rem;
                left: 1rem;
                width: 28px;
                cursor: pointer;

                img {
                    width: 100%;
                }
            }
        }
    }

    .desktop {
        display: none;

        @media (min-width: 1024px) {
            display: block;
        }

        figure.logo {
            margin: 35% auto 0 auto;
            width: 120px;
            cursor: pointer;
        }

        .container {
            margin-top: 35%;
        }
    }
}
