article.admin-title--component {
    margin-bottom: 3rem;

    @media (min-width: 768px) {
        margin-bottom: 4rem;
    }

    h1 {
        font-weight: bold;
        display: inline-block;
        font-size: 18px;

        @media (min-width: 768px) {
            font-size: 22px;
        }
        @media (min-width: 1366px) {
            font-size: 24px;
        }

        ul {
            display: inline-block;
            margin-left: 2rem;

            li {
                float: left;
                font-size: 11px;
                font-weight: normal;
                opacity: 0.5;

                &:first-child {
                    margin-right: 1rem;
                    padding-right: 1rem;
                    border-right: 1px solid rgba(112, 112, 112, 0.2);
                }

                @media (min-width: 768px) {
                    font-size: 12px;
                }

                &.active {
                    opacity: 1;
                }
            }
        }
    }
}
