section#admin-dawn {
    article.dawn-input--container {
        display: flex;
        flex-direction: column;
        margin-top: 3rem;

        @media (min-width: 768px) {
            flex-direction: row;
            margin-top: 7rem;
        }

        .diet--wrap,
        .common--wrap {
            width: 100%;
            min-height: 200px;
            // border: 1px solid red;

            @media (min-width: 1024px) {
                font-size: 15px;
            }

            h3 {
                font-size: 14px;
                text-align: center;
                margin-bottom: 2rem;

                @media (min-width: 1024px) {
                    font-size: 15px;
                }
            }

            .confirm--button--component {
                background-color: rgb(128, 211, 142);
                height: 30px;

                &.alone {
                    max-width: 70%;
                    margin: 0 auto;
                }

                @media (min-width: 1024px) {
                    height: 35px;
                }
            }
        }

        .diet--wrap {
            input#dawn-flatpickr {
                background-color: rgb(62, 62, 62);
                color: white;
                width: 100px;
                height: 33px;
                text-align: center;
                border: 0;
            }
        }

        .middle--line {
            border-bottom: 1px solid rgba(112, 112, 112, 0.5);
            margin: 3rem 0;

            @media (min-width: 768px) {
                border: 0;
                border-right: 1px solid rgba(112, 112, 112, 0.5);
                margin: 0 2rem;
            }
        }
    }
}
