label.custom {
    &.label-input,
    &.label-textarea,
    &.label-image {
        display: flex;
        align-items: center;
        height: 30px;
        margin-bottom: 1.5rem;

        $var: 7;
        @for $i from 1 through 4 {
            &.span-width-#{$var}0 {
                span {
                    width: #{$var}0px;
                    $var: $var + 1;
                }
            }
        }

        span {
            order: 1;
            width: 100px;
            color: rgba(62, 62, 62, 1);
            // margin-right: 0.8rem;
            cursor: pointer;
        }

        input,
        textarea {
            order: 2;
            flex: 1;
            height: 100%;
            font-size: 15px;
            padding: 0 0.5rem;
            border: none;
            border-radius: 0;
            border: 1px solid rgba(62, 62, 62, 0.5);
            // border: 1px solid rgba(0, 0, 0, 0.8);

            // & + span {
            //     color: black;
            // }

            // &:placeholder-shown {
            //     order: 2;
            //     flex: 1;
            //     height: 100%;
            //     font-size: 15px;
            //     padding: 0 0.5rem;
            //     border: none;
            //     border-radius: 0;
            //     border: 1px solid rgba(0, 0, 0, 0.3);

            //     &:focus {
            //         border: 1px solid rgba(0, 0, 0, 0.6);

            //         & + span {
            //             color: rgba(0, 0, 0, 0.6);
            //         }
            //     }

            //     & + span {
            //         color: rgba(0, 0, 0, 0.3);
            //     }
            // }
        }
    }

    &.label-input {
        position: relative;
        font-size: 13px;

        @media (min-width: 768px) {
            font-size: 14px;
            height: 35px;
        }

        .warning {
            display: none;
        }

        &.error {
            .warning {
                display: inline-block;
                position: absolute;
                bottom: -12px;
                left: 100px;
                color: rgba(190, 23, 23, 1);
                font-size: 9px !important;

                @media (min-width: 768px) {
                    font-size: 10px !important;
                }
            }
        }
    }

    &.label-textarea {
        align-items: flex-start !important;
        height: 150px !important;

        textarea {
            padding: 0.5rem !important;
            border: 1px solid rgba(0, 0, 0, 0.8) !important;

            &:placeholder-shown {
                border: 1px solid rgba(0, 0, 0, 0.3) !important;

                &:focus {
                    border-bottom: 1px solid rgba(0, 0, 0, 0.6) !important;
                }
            }
        }
    }

    &.label-image {
        figure {
            width: 50px;

            img {
                cursor: pointer;
            }
        }

        input {
            border-bottom: 1px solid rgba(0, 0, 0, 0.8) !important;

            &:placeholder-shown {
                border-bottom: 1px solid rgba(0, 0, 0, 0.3) !important;

                &:focus {
                    border-bottom: 1px solid rgba(0, 0, 0, 0.6) !important;

                    & + figure {
                        opacity: 0.6;
                    }
                }

                & + figure {
                    opacity: 0.3;
                }
            }
        }
    }
}
