section#admin-member {
    &.revise {
        .container-500 {
            .address {
                .represent-address,
                .detail-address {
                    flex: 1;
                }

                .postalcode {
                    max-width: 80px;
                    margin-right: 0.5rem;
                }

                .confirm--button--component {
                    margin-bottom: 0.5rem;
                    margin-left: 0.5rem;
                }
            }

            .id--area {
                .admin-input {
                    flex: 1;
                }

                .confirm--button--component {
                    margin-left: 0.5rem;
                }
            }

            .phone {
                .contents {
                    .flex {
                        & > * {
                            width: 32%;
                        }
                        input {
                            text-align: center;
                        }
                    }
                }
            }

            .grade {
                select {
                    min-width: 100px;
                }
            }
        }
    }
}
