.itemcard--wrap {
    position: relative;
    margin-bottom: 3rem;

    figure {
        position: relative;
        width: 100%;
        overflow: hidden;

        min-width: 350px;
        min-height: 350px;
        max-width: 350px;
        max-height: 350px;

        @media (min-width: 1024px) {
            min-width: 320px;
            min-height: 320px;
            max-width: 320px;
            max-height: 320px;
        }
        @media (min-width: 1366px) {
            min-width: 310px;
            min-height: 310px;
            max-width: 310px;
            max-height: 310px;
        }
        @media (min-width: 1680px) {
            min-width: 350px;
            min-height: 350px;
            max-width: 350px;
            max-height: 350px;
        }

        img {
            width: 350px;
            height: 350px;
            object-fit: cover;

            @media (min-width: 1024px) {
                width: 320px;
                height: 320px;
            }
            @media (min-width: 1366px) {
                width: 310px;
                height: 310px;
            }
            @media (min-width: 1680px) {
                width: 350px;
                height: 350px;
            }

            &.default {
                border: 1px solid rgba(112, 112, 112, 1);
            }
        }

        .blur {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            color: white;
            background-color: rgba(116, 116, 116, 0.69);
            font-size: 16px;

            @media (min-width: 768px) {
                font-size: 18px;
            }
        }
    }

    .name {
        // position: absolute;
        // left: 0;
        // bottom: -65px;
        width: 100%;
        font-size: 15px;
        line-height: 1.8;
        margin-top: 0.5rem;

        p {
            font-weight: bold;
        }

        @media (min-width: 768px) {
            font-size: 18px;

            p {
                font-size: 16px;
            }
        }
    }

    .hit,
    .new,
    .soldout {
        position: absolute;
        top: 0;
        left: 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 3px;
        display: none;
        color: white;
        width: 50px;
        height: 50px;

        &.active {
            display: flex;
        }

        @media (min-width: 768px) {
            font-size: 15px;
            width: 55px;
            height: 55px;
        }
        @media (min-width: 1024px) {
            width: 60px;
            height: 60px;
        }
        @media (min-width: 1366px) {
            font-size: 16px;
            width: 65px;
            height: 65px;
        }
        @media (min-width: 1440px) {
            width: 70px;
            height: 70px;
        }
        @media (min-width: 1680px) {
            width: 75px;
            height: 75px;
        }
    }

    .hit {
        background-color: rgb(204, 50, 66);
    }
    .new {
        background-color: rgb(149, 188, 86);
    }
    .soldout {
        background-color: rgb(242, 170, 75);

        span {
            margin-top: 3px;
        }
    }
}
