@import './layout/navigation';

main#admin-root-container {
    $maxWidth: 1440px;

    width: 100%;
    max-width: $maxWidth;
    min-height: 100vh;
    padding: 90px 0.5rem 90px 0.5rem;

    @media (min-width: 768px) {
        padding: 100px 1rem 100px 1rem;
    }

    @media (min-width: 1024px) {
        padding-top: 100px;
        padding-bottom: 100px;
        padding-left: calc(250px + 1rem);
        padding-right: 1rem;
    }

    @media (min-width: 1366px) {
        padding-top: 120px;
        padding-bottom: 120px;
        padding-left: calc(300px + 3rem);
        padding-right: 3rem;
    }

    @media (min-width: 1440px) {
        padding-left: calc(300px + 4rem);
        padding-right: 4rem;
    }

    @media (min-width: 1680px) {
        max-width: calc(#{$maxWidth} + 53px);
        padding-left: calc(300px + 6rem);
        padding-right: 6rem;
    }

    & > section {
        &.revise {
            .container-500 {
                max-width: 500px;
                margin: 0 auto;

                @media (min-width: 1680px) {
                    margin: 0;
                }
            }
        }
    }

    .confirm--button--component {
        min-width: 80px;
        height: 28px;
        font-size: 12px;
        margin: 0;

        @media (min-width: 768px) {
            height: 30px;
            font-size: 14px;
        }
    }

    ul.pagination {
        margin: 3rem auto 0;

        @media (min-width: 768px) {
            margin: 4rem auto 0;
        }
    }

    .custom-input--group {
        position: relative;
        display: flex;
        margin-bottom: 1.5rem;
        font-size: 13px;

        @media (min-width: 768px) {
            font-size: 14px;
        }

        .title {
            min-width: 100px;
            height: 30px;
            display: flex;
            align-items: center;

            @media (min-width: 768px) {
                height: 35px;
            }
        }

        .contents {
            flex: 1;
        }

        .confirm--button--component {
            height: 30px;

            @media (min-width: 768px) {
                height: 35px;
            }
        }

        input.admin-input {
            background-color: #ffffff;
        }

        .warning {
            display: none;
        }

        &.error {
            .warning {
                display: inline-block;
                position: absolute;
                bottom: -12px;
                left: 100px;
                color: rgba(190, 23, 23, 1);
                font-size: 9px !important;

                @media (min-width: 768px) {
                    font-size: 10px !important;
                }
            }
        }

        .possible {
            display: inline-block;
            position: absolute;
            bottom: -12px;
            left: 100px;
            font-size: 9px !important;
            color: rgba(23, 81, 190, 1);

            @media (min-width: 768px) {
                font-size: 10px !important;
            }
        }
    }

    select.select--component {
        height: 30px;
        font-size: 13px;
        padding: 0 1.5rem 0 1rem;

        @media (min-width: 768px) {
            height: 35px;
            font-size: 14px;
        }

        &.board-select {
            height: 28px;
            border: 0;
            border-left: 1px solid rgba(112, 112, 112, 0.4);
            opacity: 0.5;
            padding: 0;
            padding-left: 0.5rem;
            text-align-last: left;
            background-position: 85% center;

            &.min-100 {
                min-width: 110px;
            }
        }
    }

    input {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        font-size: 13px;

        &.admin-input {
            height: 30px;
            padding: 0 0.5rem;
            border: none;
            border-radius: 0;
            border: 1px solid rgba(62, 62, 62, 0.5);

            @media (min-width: 768px) {
                height: 35px;
            }
        }

        @media (min-width: 768px) {
            font-size: 14px;
        }

        &::placeholder {
            /* Chrome, Firefox, Opera, Safari 10.1+ */
            color: rgba(112, 112, 112, 0.4);
            opacity: 1;
            font-size: 12px;
            /* Firefox */
            @media (min-width: 768px) {
                font-size: 13px;
            }
        }

        &:-ms-input-placeholder {
            /* Internet Explorer 10-11 */
            color: rgba(112, 112, 112, 0.4);
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 13px;
            }
        }

        &::-ms-input-placeholder {
            /* Microsoft Edge */
            color: rgba(112, 112, 112, 0.4);
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 13px;
            }
        }
    }

    .cyan {
        color: rgba(128, 211, 142, 1);
    }

    .caution,
    .information {
        margin-top: 0.3rem;
        font-size: 10px !important;

        @media (min-width: 768px) {
            font-size: 11px !important;
        }
    }

    .caution {
        color: rgba(190, 23, 23, 1);
    }

    .information {
        color: rgba(23, 81, 190, 1);
    }

    @import './pages/notice', './pages/member', './pages/order', './pages/product', './pages/dawn';

    @import './mixins/admin_title', './mixins/label-input-textarea';

    @import './style/table_basic';
}
