section#detail {
    .container {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding: 0 1rem;

        @media (min-width: 768px) {
            padding: 0 1.5rem;
        }
        @media (min-width: 1366px) {
            padding: 0 2rem;
        }
        @media (min-width: 1680px) {
            padding: 0;
        }

        article.head {
            display: flex;
            flex-wrap: wrap;
            padding: 4rem 0;

            @media (min-width: 768px) {
                padding: 8rem 0;
            }

            .represent--image-wrap {
                width: 100%;

                @media (min-width: 768px) {
                    width: 40%;
                }

                figure {
                    position: relative;
                    min-width: 330px;
                    min-height: 330px;
                    max-width: 330px;
                    max-height: 330px;
                    overflow: hidden;

                    @media (min-width: 375px) {
                        min-width: 343px;
                        min-height: 343px;
                        max-width: 343px;
                        max-height: 343px;
                    }
                    @media (min-width: 380px) {
                        min-width: 353px;
                        min-height: 353px;
                        max-width: 353px;
                        max-height: 353px;
                    }

                    @media (min-width: 400px) {
                        min-width: 380px;
                        min-height: 380px;
                        max-width: 380px;
                        max-height: 380px;
                    }

                    @media (min-width: 768px) {
                        min-width: 310px;
                        min-height: 310px;
                        max-width: 310px;
                        max-height: 310px;
                    }
                    @media (min-width: 1024px) {
                        min-width: 400px;
                        min-height: 400px;
                        max-width: 400px;
                        max-height: 400px;
                    }

                    @media (min-width: 1366px) {
                        min-width: 500px;
                        min-height: 500px;
                        max-width: 500px;
                        max-height: 500px;
                    }

                    img {
                        object-fit: cover;
                        width: 330px;
                        height: 330px;

                        @media (min-width: 375px) {
                            width: 343px;
                            height: 343px;
                        }
                        @media (min-width: 380px) {
                            width: 353px;
                            height: 353px;
                        }

                        @media (min-width: 400px) {
                            width: 380px;
                            height: 380px;
                        }

                        @media (min-width: 768px) {
                            width: 310px;
                            height: 310px;
                        }
                        @media (min-width: 1024px) {
                            width: 400px;
                            height: 400px;
                        }

                        @media (min-width: 1366px) {
                            width: 500px;
                            height: 500px;
                        }
                    }
                }
            }

            .sale--data-wrap {
                width: 100%;
                padding-top: 2rem;

                @media (min-width: 768px) {
                    padding-top: 0;
                    padding-left: 3rem;
                    width: 60%;
                }
                @media (min-width: 1366px) {
                    padding-left: 7rem;
                }

                hgroup {
                    h1 {
                        font-size: 22px;
                        margin-bottom: 0.7rem;

                        @media (min-width: 768px) {
                            margin-bottom: 1rem;
                            font-size: 32px;
                        }
                    }

                    span {
                        color: rgb(128, 211, 142);
                        font-size: 15px;

                        @media (min-width: 768px) {
                            font-size: 16px;
                        }
                    }
                }

                .rating {
                    display: flex;
                    margin: 1rem 0 2rem;

                    // @media (min-width: 1024px) {
                    //     margin: 1rem 0 3rem;
                    // }
                    @media (min-width: 1440px) {
                        margin: 2rem 0 3rem;
                    }

                    figure {
                        position: relative;
                        width: 100px;
                        overflow: hidden;
                        margin-right: 0.8rem;
                        margin-top: -1px;

                        @media (min-width: 768px) {
                            width: 110px;
                        }

                        img {
                            width: 100%;
                            height: 100%;
                            position: relative;
                            z-index: 2;
                        }

                        figcaption {
                            @include centeredY();
                            left: -100%;
                            width: 100%;
                            height: 88%;
                            background-color: rgb(128, 211, 142);

                            &.average--05 {
                                left: -92.5%;
                            }
                            &.average--10 {
                                left: -81%;
                            }
                            &.average--15 {
                                left: -71%;
                            }
                            &.average--20 {
                                left: -61%;
                            }
                            &.average--25 {
                                left: -49.95%;
                            }
                            &.average--30 {
                                left: -40%;
                            }
                            &.average--35 {
                                left: -29%;
                            }
                            &.average--40 {
                                left: -18%;
                            }
                            &.average--45 {
                                left: -7.5%;
                            }
                            &.average--50 {
                                left: 0;
                            }
                        }
                    }

                    b {
                        font-size: 17px;
                        font-weight: bolder;

                        @media (min-width: 768px) {
                            font-size: 18px;
                        }
                    }
                }

                ul {
                    li {
                        display: flex;
                        min-height: 30px;
                        font-size: 13px;

                        @media (min-width: 1024px) {
                            min-height: 34px;
                            font-size: 15px;
                        }
                        @media (min-width: 1440px) {
                            min-height: 40px;
                            font-size: 16px;
                            margin-bottom: 0.3rem;
                        }

                        &:first-child {
                            .box {
                                margin-top: -3px;
                                font-weight: bolder;
                                font-size: 15px;

                                @media (min-width: 768px) {
                                    margin-top: -5px;
                                    font-size: 16px;
                                }

                                b {
                                    margin-right: 0.5rem;
                                    font-size: 18px;

                                    @media (min-width: 768px) {
                                        font-size: 22px;
                                    }
                                }
                            }
                        }

                        &:nth-child(2) {
                            .box {
                            }
                        }

                        .title {
                            min-width: 80px;

                            @media (min-width: 768px) {
                                min-width: 100px;
                            }
                        }
                    }
                }

                .total--wrap {
                    position: relative;
                    height: 50px;
                    margin-bottom: 1.5rem;

                    @media (min-width: 768px) {
                        margin-bottom: 2.5rem;
                        height: 60px;
                    }

                    div {
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        font-size: 14px;

                        @media (min-width: 768px) {
                            font-size: 15px;
                        }

                        b.price {
                            font-size: 22px;
                            font-weight: bolder;
                            margin: 0 0.3rem 0 1rem;
                            color: rgb(128, 211, 142);

                            @media (min-width: 768px) {
                                margin: 0 0.3rem 0 1.3rem;
                                font-size: 35px;
                            }
                        }

                        span {
                            font-weight: bolder;
                        }
                    }
                }

                .buttons--wrap {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    @media (min-width: 1024px) {
                        flex-direction: row;
                    }

                    .sold-out,
                    button,
                    input[type='submit'] {
                        height: 36px;
                        width: 100%;
                        font-size: 15px;

                        @media (min-width: 768px) {
                            height: 44px;
                            font-size: 16px;
                        }
                        @media (min-width: 1024px) {
                            max-width: 260px;
                            height: 44px;
                            // font-size: 16px;
                        }
                        @media (min-width: 1366px) {
                            max-width: 320px;
                            height: 44px;
                            // font-size: 15px;
                        }
                        @media (min-width: 1440px) {
                            max-width: 360px;
                            height: 44px;
                            // font-size: 15px;
                        }
                    }

                    button {
                        border: 1px solid rgba(0, 0, 0, 0.5);
                        background-color: white;
                    }

                    input[type='submit'] {
                        border: 0;
                        border-radius: 0;
                        background-color: rgb(128, 211, 142);
                        color: white;
                    }

                    .sold-out {
                        position: absolute;
                        background-color: rgba(100, 100, 100, 0.3);
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        top: 0;
                        right: 0;
                    }
                }
            }
        }

        article.description,
        article.exchange-refund,
        article.appraise {
            .scrolling--wrap {
                display: flex;
                align-items: flex-end;
                background-color: rgba(62, 62, 62, 1);
                height: 40px;
                margin: 0 -1rem;

                @media (min-width: 768px) {
                    margin: 0;
                    height: 50px;
                }

                a[data-scroll] {
                    width: 100%;
                    height: 37px;
                    @include flexCentered();

                    @media (min-width: 768px) {
                        width: 150px;
                        height: 47px;
                    }
                    @media (min-width: 1024px) {
                        width: 180px;
                        height: 47px;
                    }

                    &.white {
                        background-color: white;
                    }

                    &.black {
                        background-color: rgba(62, 62, 62, 1);
                        color: white;
                    }
                }
            }
        }

        article.description {
            #product-description {
                position: relative;
                padding: 3rem 0;
                font-size: 14px;
                line-height: 1.6;
                width: 100%;
                max-width: 1000px;
                margin: 0 auto;

                @media (min-width: 768px) {
                    padding: 5rem 0;
                    font-size: 16px;
                    line-height: 1.8;
                }

                &.empty {
                    &:before {
                        content: '제품 설명이 존재하지 않습니다.';
                        display: inline-block;
                        width: 100%;
                        padding: 100px 0;
                        text-align: center;
                    }
                }

                img {
                    display: block;
                    margin: 12px auto;
                    // width: auto !important;
                    height: auto !important;
                    max-width: 100% !important;
                    // max-height: 500px !important;
                    object-fit: contain;
                }

                .youtube-wrap {
                    position: relative;
                    width: 100%;
                    padding-bottom: 56.25%;

                    iframe {
                        position: absolute;
                        width: 100%;
                        height: 100%;
                    }
                }

                p,
                div {
                    margin: 0;
                }

                h1,
                h2,
                h3,
                h4,
                h5,
                h6 {
                    font-weight: bold;
                    margin: 0;
                    margin-bottom: 12px;
                }

                h1 {
                    font-size: 24px;
                }

                h2 {
                    font-size: 20px;
                }

                h3 {
                    font-size: 18px;
                }

                strong,
                b {
                    font-weight: bold;
                }

                blockquote {
                    padding-left: 20px;
                    padding-right: 8px;
                    border-left: 5px solid #ccc;
                }

                ul,
                ol {
                    list-style: initial;
                    display: block;
                    margin-block-start: 1rem;
                    margin-block-end: 1rem;
                    margin-inline-start: 0px;
                    margin-inline-end: 0px;
                    padding-inline-start: 20px;
                }

                a {
                    overflow: hidden;
                    word-break: break-all;
                    // color: rgb(120, 155, 221);
                    text-decoration: underline !important;
                }

                em {
                    font-style: italic;
                }
            }
        }

        article.appraise {
            #product-rating {
                padding: 4rem 0 3rem;

                @media (min-width: 768px) {
                    padding: 5rem 0 4rem;
                }

                .flex {
                    &:first-child {
                        input {
                            min-width: 55px;
                            max-width: 55px;
                            text-align: center;
                            border: 0;
                            margin-left: 1rem;
                            font-size: 30px;
                            font-weight: bolder;

                            @media (min-width: 768px) {
                                margin-left: 2rem;
                                font-size: 40px;
                                max-width: 70px;
                                min-width: 70px;
                            }
                        }
                    }
                    &:last-child {
                        padding-top: 2rem;
                        font-size: 15px;

                        @media (min-width: 768px) {
                            font-size: 18px;
                        }
                    }

                    #rating--value-box {
                        background-color: white;
                    }
                }
            }
        }

        .exchange--refund-wrap {
            margin: 4rem 0;
            font-size: 12px;

            @media (min-width: 768px) {
                padding: 3rem 0;
                font-size: 13px;
            }

            .box {
                max-width: 510px;
                margin: 0 auto;

                &.w-max-800 {
                    max-width: 820px;
                }
                &.w-max-1000 {
                    max-width: 1000px;
                }

                h1,
                h2 {
                    text-align: center;
                }

                h1 {
                    font-weight: bolder;
                    font-size: 16px;

                    @media (min-width: 768px) {
                        font-size: 21px;
                    }
                }

                h2 {
                    color: rgba(62, 62, 62, 0.2);
                    font-size: 18px;
                    margin-top: 4rem;

                    @media (min-width: 768px) {
                        font-size: 23px;
                    }
                }
            }

            figure {
                min-width: 90px;

                @media (min-width: 768px) {
                    min-width: 170px;
                }
                @media (min-width: 1024px) {
                    min-width: 200px;
                }

                img {
                    display: block;
                    margin: 0 auto;
                    width: 80%;
                    max-width: 90px;

                    &.money {
                        margin-top: -12px;
                    }

                    &.boxs {
                        margin-top: -3px;
                        max-width: 95px;
                    }
                }
            }

            ul {
                flex: 1;
                line-height: 2;

                li {
                    &:last-child {
                        font-size: 11px;
                        color: rgba(62, 62, 62, 0.4);
                    }

                    .dot {
                        min-width: 25px;
                        text-align: center;

                        @media (min-width: 768px) {
                            min-width: 30px;
                        }
                    }

                    .contents {
                        .attach {
                            padding-left: 0.5rem;

                            @media (min-width: 768px) {
                                padding-left: 0.8rem;
                            }
                        }
                        p {
                            font-size: 11px;

                            @media (min-width: 768px) {
                                font-size: 12px;
                            }
                        }
                    }
                }
            }
        }
    }
}
