section#leave {
    .area {
        border-top: 5px solid rgb(62, 62, 62);
        padding-top: 3rem;

        @media (min-width: 768px) {
            padding-top: 5rem;
        }

        article {
            width: 100%;
            max-width: 500px;
            margin: 0 auto;

            .group-wrap {
                margin-bottom: 2rem;

                @media (min-width: 768px) {
                    margin-bottom: 3rem;
                }

                // label {
                .title {
                    font-weight: bold;
                    color: black;
                    font-size: 10px;

                    @media (min-width: 768px) {
                        font-size: 12px;
                    }

                    &::before {
                        content: '•';
                        margin: 0 0.5rem;
                    }
                }
                // }

                p {
                    margin-top: 0.5rem;
                    padding-left: 1.3rem;
                    font-size: 10px;

                    @media (min-width: 768px) {
                        font-size: 11px;
                    }
                }

                &.check {
                    label {
                        span {
                            color: rgb(62, 62, 62);
                            font-weight: normal;
                        }
                    }
                }
            }

            .comment-box {
                border: 1px solid rgba(112, 112, 112, 0.5);
                padding: 2rem 0;
                font-size: 12px;
                text-align: center;

                @media (min-width: 768px) {
                    font-size: 13px;
                }
            }

            .inputbox--component {
                max-width: 350px;
                margin: 0 auto;
                padding-top: 2rem;
                margin-bottom: 3rem;
            }

            .confirm--button--component {
                width: 100%;
                max-width: 350px;
                margin: 0 auto;
            }
        }
    }
}
