#file-upload.attached-file {
    .select-wrap {
        display: flex;
        align-items: center;

        &:first-child {
            margin-bottom: 0.5rem;
        }

        .upload-btn-wrapper {
            position: relative;
            overflow: hidden;
            display: inline-block;

            figure.upload-btn {
                display: flex;
                border: 1px solid #92aab0;
                color: #92aab0;
                background-color: white;
                padding: 0.4rem 1rem;
                border-radius: 4px;
                font-size: 14px;
                cursor: pointer;

                img {
                    width: 18px;
                    height: 18px;
                    opacity: 0.35;
                    margin-right: 0.3rem;
                }
            }
        }

        .count-wrap {
            display: inline-block;
            margin-left: 0.5rem;
            font-size: 13px;
            color: rgba(0, 0, 0, 0.6);
        }
    }

